/* Risk Dashboard Styles - Modern UI */
.risk-dashboard-container {
  max-width: 100%;
  margin: 0 auto;
  padding: 1.5rem;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  background: #f8fafc;
  min-height: 100vh;
}

/* Header Styles */
.risk-dashboard-header {
  background: linear-gradient(135deg, #1e293b 0%, #334155 100%);
  padding: 2rem;
  margin-bottom: 2rem;
  border-radius: 12px;
  color: white;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.header-title {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.header-icon {
  color: #60a5fa;
  width: 2.5rem;
  height: 2.5rem;
}

.header-title h1 {
  margin: 0;
  font-size: 1.8rem;
  font-weight: 800;
  background: linear-gradient(135deg, #60a5fa 0%, #34d399 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-shadow: 0 2px 10px rgba(96, 165, 250, 0.3);
  letter-spacing: -0.5px;
}

.header-title p {
  margin: 0;
  font-size: 1rem;
  color: #cbd5e1;
  font-style: italic;
}

.site-overview {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  margin-top: 1rem;
}

.site-info,
.site-metrics {
  display: flex;
  gap: 2rem;
  flex-wrap: wrap;
}

.site-info span,
.site-metrics span {
  color: #e2e8f0;
  font-size: 0.9rem;
  font-weight: 500;
}

/* Risk Sections Grid */
.risk-sections-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

/* Risk Section Styles */
.risk-section {
  background: white;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 10px -2px rgba(0, 0, 0, 0.08);
  transition: all 0.3s ease;
  border: 1px solid rgba(203, 213, 225, 0.5);
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.risk-section:hover {
  box-shadow: 0 12px 20px -5px rgba(0, 0, 0, 0.12);
  transform: translateY(-3px);
}

.risk-section::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 12px;
  background: radial-gradient(circle at top right, rgba(255, 255, 255, 0.2), transparent);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.risk-section:hover::after {
  opacity: 1;
}

.risk-section::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 4px;
  background: linear-gradient(to bottom, #3b82f6, #1d4ed8);
  border-top-left-radius: 12px;
  border-bottom-left-radius: 12px;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  cursor: pointer;
  background: linear-gradient(135deg, #f8fafc, #f1f5f9);
  border-bottom: 1px solid #e2e8f0;
  position: relative;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.03);
  transition: background 0.2s ease;
}

.section-header:hover {
  background: linear-gradient(135deg, #f1f5f9, #e2e8f0);
}

.section-title {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-weight: 700;
  font-size: 1rem;
  color: #1e293b;
  transition: all 0.3s ease;
}

.risk-section:hover .section-title {
  transform: translateX(2px);
}

.section-number {
  font-size: 1rem;
  min-width: 2rem;
  color: #3b82f6;
  transition: transform 0.3s ease;
}

.risk-section:hover .section-number {
  transform: scale(1.15);
}

.chevron {
  width: 1.25rem;
  height: 1.25rem;
  color: #64748b;
  transition: transform 0.3s ease;
}

.chevron.expanded {
  transform: rotate(90deg);
  color: #3b82f6;
}

.section-content {
  padding: 1.5rem;
  font-size: 0.95rem;
  line-height: 1.6;
  color: #475569;
}

/* Credit Grades */
.credit-grades {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
}

.grade {
  padding: 0.5rem 1rem;
  border-radius: 6px;
  font-weight: 600;
  font-size: 0.9rem;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.grade:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.grade-a {
  background: linear-gradient(135deg, #10b981, #059669);
  color: white;
  border-left: 3px solid #047857;
}

.grade-b {
  background: linear-gradient(135deg, #3b82f6, #2563eb);
  color: white;
  border-left: 3px solid #1d4ed8;
}

.grade-c {
  background: linear-gradient(135deg, #f59e0b, #d97706);
  color: white;
  border-left: 3px solid #b45309;
}

.grade-d {
  background: linear-gradient(135deg, #ef4444, #dc2626);
  color: white;
  border-left: 3px solid #b91c1c;
}

/* Decay Stats */
.decay-stats {
  display: flex;
  gap: 1.5rem;
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
}

.decay-stats span {
  color: #334155;
  font-size: 0.95rem;
  font-weight: 500;
  background: #f1f5f9;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  transition: all 0.2s ease;
  border-left: 2px solid #3b82f6;
}

.decay-stats span:hover {
  background: #e2e8f0;
  transform: translateY(-2px);
}

/* Efficiency Stats */
.efficiency-stats {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
  align-items: center;
  flex-wrap: wrap;
}

.efficiency-stats span {
  color: #334155;
  font-size: 0.95rem;
  font-weight: 500;
  background: #f1f5f9;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  transition: all 0.2s ease;
  border-left: 2px solid #60a5fa;
}

.efficiency-stats span:hover {
  background: #e2e8f0;
  transform: translateY(-2px);
}

.overexposed {
  color: #b91c1c !important;
  font-weight: 600;
  border-left: 2px solid #ef4444 !important;
}

/* Funnel Ratios */
.funnel-ratios {
  display: flex;
  gap: 1.5rem;
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
}

.funnel-ratios span {
  color: #334155;
  font-size: 0.95rem;
  font-weight: 500;
  background: #f1f5f9;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  transition: all 0.2s ease;
  border-left: 2px solid #8b5cf6;
}

.funnel-ratios span:hover {
  background: #e2e8f0;
  transform: translateY(-2px);
}

/* AI vs Human Stats */
.ai-human-stats {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 1.5rem;
  padding: 1.25rem;
  background: #f8fafc;
  border-radius: 8px;
  border: 1px solid #e2e8f0;
}

.stat-group {
  display: flex;
  gap: 1.5rem;
  flex-wrap: wrap;
}

.stat-group span {
  color: #334155;
  font-size: 0.95rem;
  font-weight: 500;
  background: #f1f5f9;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  transition: all 0.2s ease;
  border-left: 2px solid #10b981;
}

.stat-group span:hover {
  background: #e2e8f0;
  transform: translateY(-2px);
}

/* Risk Details */
.risk-details {
  margin-bottom: 1.5rem;
  padding: 1.25rem;
  background: #f8fafc;
  border-radius: 8px;
  border: 1px solid #e2e8f0;
}

.risk-details p {
  margin: 0.5rem 0;
  color: #475569;
  font-size: 0.95rem;
  font-weight: 400;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.risk-details p::before {
  content: '';
  color: #3b82f6;
  font-weight: bold;
}

.dropper-item,
.overexertion-item {
  color: #b45309 !important;
  font-weight: 500;
  background: rgba(245, 158, 11, 0.1);
  padding: 0.5rem 0.75rem;
  border-radius: 6px;
  margin-left: 1.5rem;
}

/* View More Button */
.view-more-btn {
  background: linear-gradient(135deg, #f8fafc, #f1f5f9);
  border: 1px solid #e2e8f0;
  color: #3b82f6;
  padding: 0.75rem 1.25rem;
  border-radius: 8px;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 600;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
}

.view-more-btn:hover {
  border-color: #3b82f6;
  background: #f1f5f9;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.15);
}

/* Error Pages Summary */
.error-pages-summary {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(135deg, #fff5f5, #fee2e2);
  border: 1px solid #fecaca;
  border-radius: 12px;
  padding: 1.5rem;
  margin-bottom: 2rem;
  box-shadow: 0 4px 15px rgba(239, 68, 68, 0.1);
}

.error-header {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  color: #b91c1c;
  font-weight: bold;
}

.error-icon {
  width: 1.5rem;
  height: 1.5rem;
  color: #dc2626;
}

.error-stats {
  display: flex;
  gap: 2rem;
  color: #7f1d1d;
  font-size: 0.95rem;
  font-weight: 500;
}

/* Action Buttons */
.risk-dashboard-actions {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 2.5rem;
}

.action-btn {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.9rem 1.8rem;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.action-btn.primary {
  background: linear-gradient(135deg, #3b82f6 0%, #1d4ed8 100%);
  color: white;
  border: none;
}

.action-btn.primary:hover {
  background: linear-gradient(135deg, #2563eb 0%, #1e40af 100%);
  transform: translateY(-3px);
  box-shadow: 0 6px 15px rgba(59, 130, 246, 0.3);
}

.action-btn.secondary {
  background: white;
  color: #3b82f6;
  border: 1px solid #3b82f6;
}

.action-btn.secondary:hover {
  background: #f1f5f9;
  transform: translateY(-3px);
  box-shadow: 0 6px 15px rgba(59, 130, 246, 0.15);
}

/* Responsive Design */
@media (max-width: 768px) {
  .risk-dashboard-container {
    padding: 1rem;
  }

  .risk-sections-grid {
    grid-template-columns: 1fr;
  }

  .site-info,
  .site-metrics {
    flex-direction: column;
    gap: 0.75rem;
  }

  .error-pages-summary {
    flex-direction: column;
    gap: 1.25rem;
    text-align: center;
  }

  .risk-dashboard-actions {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }

  .action-btn {
    width: 100%;
    max-width: 300px;
  }
}

@media (max-width: 480px) {
  .risk-dashboard-header {
    padding: 1.5rem;
  }

  .header-title {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.75rem;
    padding-bottom: 1rem;
  }

  .header-title h1 {
    font-size: 1.5rem;
  }

  .section-title {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }

  .section-number {
    min-width: auto;
  }

  .credit-grades,
  .decay-stats,
  .efficiency-stats,
  .funnel-ratios {
    flex-direction: column;
    gap: 0.75rem;
    align-items: flex-start;
  }

  .grade,
  .decay-stats span,
  .efficiency-stats span,
  .funnel-ratios span {
    width: 100%;
  }
}

/* Modern scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #f1f5f9;
}

::-webkit-scrollbar-thumb {
  background: #94a3b8;
  border-radius: 8px;
}

::-webkit-scrollbar-thumb:hover {
  background: #64748b;
}

/* Subtle highlight effect */
@keyframes highlightPulse {
  0% {
    box-shadow: 0 0 0 0 rgba(59, 130, 246, 0.2);
  }

  70% {
    box-shadow: 0 0 0 8px rgba(59, 130, 246, 0);
  }

  100% {
    box-shadow: 0 0 0 0 rgba(59, 130, 246, 0);
  }
}

.highlight-pulse {
  animation: highlightPulse 2s infinite;
}

/* Warning effect for important notifications */
.warning-item {
  position: relative;
  background: rgba(245, 158, 11, 0.1);
  border-left: 3px solid #f59e0b;
  padding: 0.75rem 1rem;
  border-radius: 6px;
  transition: all 0.3s ease;
}

.warning-item:hover {
  background: rgba(245, 158, 11, 0.15);
  transform: translateY(-2px);
}

/* Loading Spinner Styles */
.loading-spinner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  gap: 1.5rem;
  color: #64748b;
}

.spinner {
  width: 50px;
  height: 50px;
  border: 3px solid rgba(203, 213, 225, 0.3);
  border-top: 3px solid #3b82f6;
  border-right: 3px solid #3b82f6;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}


.loading-spinner p {
  color: #64748b;
  font-size: 1rem;
  font-weight: 500;
  margin: 0;
  animation: pulse 2s ease-in-out infinite;
}

@keyframes pulse {

  0%,
  100% {
    opacity: 0.6;
  }

  50% {
    opacity: 1;
  }
}

/* High contrast mode for accessibility */
@media (prefers-contrast: high) {
  .risk-dashboard-container {
    background: #000;
    color: #fff;
  }

  .risk-section {
    border-color: #fff;
  }

  .section-header:hover {
    background: #333;
  }
}